@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&display=swap);
html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 0.5vw;
}

::-webkit-scrollbar-thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.1);
  opacity: 0.5;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.transitions-item {
  overflow: hidden;
  width: 100%;
  color: white;
  font-size: 3rem;
  font-weight: 800;
  line-height: 1.2;
  cursor: pointer;
  will-change: transform, opacity, height;
}

.trails-main {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-align-items: center;
          align-items: center;
}

.trails-text {
  position: relative;
  width: 100%;
  font-size: 3.5rem;
  font-weight: 800;
  will-change: transform, opacity;
  overflow: hidden;
}

.trails-text > div {
  overflow: hidden;
}

.about {
  margin-top: 15vh;
  text-align: center;
}

.footer a {
  color: cornflowerblue;
  text-decoration: none;
  font-weight: bold;
}

.footer a:hover {
  color: aliceblue;
}

@-webkit-keyframes upDown {
  0% {
    bottom: 20vh;
    opacity: 1;
  }

  25% {
    bottom: 15vh;
    opacity: 0.75;
  }

  50% {
    bottom: 10vh;
    opacity: 0.5;
  }

  75% {
    bottom: 5vh;
    opacity: 0.25;
  }

  100% {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes upDown {
  0% {
    bottom: 20vh;
    opacity: 1;
  }

  25% {
    bottom: 15vh;
    opacity: 0.75;
  }

  50% {
    bottom: 10vh;
    opacity: 0.5;
  }

  75% {
    bottom: 5vh;
    opacity: 0.25;
  }

  100% {
    bottom: 0;
    opacity: 0;
  }
}

